<template>
  <main id="mainContainer">
    <div class="floatingBox">
      <div class="susLogo">
        <img class="susLogoImg" :src="logo" alt="susLogo" />
      </div>
      <div class="inputBox">
        <input class="inputHome" type="text" v-model="login" placeholder="Login" />
        <input class="inputHome" type="password" v-model="senha" placeholder="Senha" />
      </div>
      <div class="inputBox btOKField">
        <input class="inputHome btOK" type="button" value="OK" @click="consultClient" />
      </div>
      <div v-if="showEMessage" class="mesagemDeErro">
        {{ deuMerda }}
      </div>
      <div v-if="false" class="hello">
        É sua primeira vez por aqui? Clique
        <span class="cliqueAqui" @click="switchPageCad">Aqui</span> para fazer seu
        cadastro.
      </div>
    </div>
  </main>
</template>

<script>
import axios from "axios";

const http = axios.create();

export default {
  data() {
    return {
      senha: null,
      login: null,
      showEMessage: false,
      deuMerda: null,
      rClient: null,
      logo: "./img/icons/sus-logo.svg",
      BASE_URL: null,
    };
  },
  mounted() {

    this.pedeNotifyPermission()
    
    this.BASE_URL = "https://vizinhoapi.herokuapp.com/"
    // this.BASE_URL = "http://localhost:3000/";

    this.verifySection();
  },
  methods: {
    pedeNotifyPermission() {
      if ("Notification" in window && Notification.permission !== "denied") {
        // Suporte a notificações e permissão não negada
        // ...
      }
      if (Notification.permission !== "granted") {
        Notification.requestPermission().then((permission) => {
          if (permission === "granted") {
            // Permissão concedida
            // ...
          }
        });
      }
    },
    verifySection() {
      let section = localStorage.getItem("secction_page");

      let lClient = localStorage.getItem("client");

      // console.log({ section });
      // console.log({ lClient });

      if (lClient === null) {
        localStorage.setItem("secction_page", "home");
        localStorage.setItem("sPrefix", null);
        localStorage.setItem("sSelection", null);
        this.$router.push("/");
      }

      if (section === null) {
        localStorage.setItem("secction_page", "home");
        localStorage.setItem("sPrefix", null);
        localStorage.setItem("sSelection", null);
        localStorage.setItem("client", null);
        this.$router.push("/");
      }

      if (lClient && section !== "home" && section !== null) {
        this.$router.push("/selecao");
      }
    },
    switchPageCad() {
      this.$router.push("/cadastro");
    },
    async verifyCad() {
      if (!!this.CPF) {
        this.CPF = this.CPF.replace(/[.,() -]/g, "");
        this.CPF = this.CPF.replace(/\D/g, "");
      }
      if (!!this.cSUS) {
        this.cSUS = this.cSUS.replace(/[.,() -]/g, "");
        this.cSUS = this.cSUS.replace(/\D/g, "");
      }

      // console.log(this.CPF,this.cSUS)

      try {
        const response = await http.post(this.BASE_URL + "clientes/sus", {
          doc: this.CPF,
          docsus: this.cSUS,
          vertype: 1,
        });

        const rConsult = response.data.response;

        if (rConsult) {
          this.rClient = rConsult.client;
          // console.log({rCliente:rConsult.client})
        }

        return rConsult.status;
      } catch (error) {
        // console.log({ error });
        return false;
      }
    },
    async consultClient() {
      if (!this.login || !this.senha) {
        // console.log("Parâmetros inválidos!");
        this.deuMerda = "Parâmetros inválidos!";
        this.showEMessage = true;

        setTimeout(() => {
          this.showEMessage = false;
        }, 5000);
      } else {
        this.$router.push("/triagem");
      }
    },
  },
};
</script>

<style scoped>
#mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.floatingBox {
  width: 360px;
  height: 500px;
  background-color: var(--vt-c-white-soft);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.inputBox {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 30%;
}

.inputHome {
  width: 85%;
  text-align: center;
  font-size: 22px;
  padding: 12px;
  border-radius: 6px;
  background-color: rgb(14, 14, 95);
  color: aliceblue;
}

.inputHome::selection {
  border: none;
  background-color: rgb(175, 175, 212);
  padding: 6px;
}

.inputHome::placeholder {
  color: aliceblue;
}

.inputHome:focus {
  caret-color: rgb(255, 255, 255);
}

.susLogo {
  margin-top: 20px;
  width: 80%;
  height: 90px;
  margin-bottom: 20px;
}

.susLogoImg {
  width: 100%;
  margin-top: -40%;
}

.hello {
  width: 80%;
  color: rgb(17, 17, 75);
  text-align: right;
}

.cliqueAqui {
  font-weight: 700;
  cursor: pointer;
}

.btOKField {
  height: 90px;
}

.btOK {
  cursor: pointer;
  font-weight: 800;
  font-size: 18px;
}

.mesagemDeErro {
  color: rgb(126, 0, 0);
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px;
  transition: 0.3s;
}

@media (max-width: 450px) {
  #mainContainer {
    background-color: var(--vt-c-white-soft);
  }
}
</style>
